//propsi otetaan vastaan nimell customer, pilkulla eriteltyn saa monta propsia
//useEffect hook, ajetaan aina kun komponentti latautuu
// useEffect(()=>{
//   EventService.getAll()
//   .then(response=>response) //muutetaan json data javascriptiksi
//   .then(oliot=>setEvents(oliot))
  
//   }
//     ,
