
import './App.css';
import React,{useState, useEffect} from 'react';
import UserService from './services/User';
import UserAdd from './UserAdd';


//propsi otetaan vastaan nimell, pilkulla eriteltyn saa monta propsia
function UserList({setIsPositive, setShowMessage, setMessage}) {

    // Usestate hook kytss, Komponentin tilan mritys
    const [users, setUsers] = useState([])

    const [lisystila, setLisystila] = useState(false)
    const [muokkaustila, setMuokkaustila] = useState(false)
    const [reload, reloadNow]=useState(false)
    const [muokattavaUser, setMuokattavaUser]=useState(false)
    const [search, setSearch]=useState('')

//useEffect hook, ajetaan aina kun komponentti latautuu
useEffect(()=>{
UserService.getAll()
.then(data=>{
  setUsers(data)
})
}
  ,
  [lisystila, reload, muokkaustila]
)

//Hakukentn Onchange tapahtumanksittelij
const handleSearchInputChange=(event)=>{

  setSearch(event.target.value.toLowerCase())
}
const editUser =(user)=>{
setMuokattavaUser(user)
setMuokkaustila(true)
}

return (
  <>
      <h1><nobr>Users</nobr>
             
      {lisystila && <UserAdd setLisystila={setLisystila} 
            setIsPositive={setIsPositive} setMessage={setMessage} setShowMessage={setShowMessage} />}

              {!lisystila && <button className="nappi btn btn-success" onClick={() => setLisystila(true)}>Add new</button>}</h1>

               {!lisystila && !muokkaustila &&
              <input placeholder='Search by Lastname' value={search} onChange={handleSearchInputChange}/>}
              
              {!lisystila && !muokkaustila &&
<table id="userTable">
    <thead>
        <tr>
        <th>Firstname</th>
        <th>Lastname</th>
        <th>Email</th>
        <th>Accesslevel</th> 
        </tr>    
    </thead>
    
<tbody >

                {users && users.map(u => {
                        const lowerCaseName= u.lastName.toLowerCase()
                        if(lowerCaseName.indexOf(search) > -1){
                        return(
                            <tr key={u.userId}>
                                <td>{u.firstName}</td>
                                <td>{u.lastName}</td>
                                <td>{u.email}</td>
                                <td>{u.accesslevelId}</td>

                            </tr>


                        )}}
                        
                    ) 
                }
                
               </tbody>
                </table>}
  </>
)
}

export default UserList;
