
import './App.css';
import React,{useState, useEffect} from 'react';
import CustomerService from './services/Customer';
import Customer from './Customer';
import CustomerAdd from './CustomerAdd'
import CustomerEdit from './CustomerEdit'


//propsi otetaan vastaan nimell, pilkulla eriteltyn saa monta propsia
function CustomerList({setIsPositive, setShowMessage, setMessage}) {

    // Usestate hook kytss, Komponentin tilan mritys
    const [customers, setCustomers] = useState([])
    const [showCustomers, setShowCustomers] = useState(false)
    const [lisystila, setLisystila] = useState(false)
    const [muokkaustila, setMuokkaustila] = useState(false)
    const [reload, reloadNow]=useState(false)
    const [muokattavaCustomer, setMuokattavaCustomer]=useState(false)
    const [search, setSearch]=useState('')

//useEffect hook, ajetaan aina kun komponentti latautuu
useEffect(()=>{
  const token=localStorage.getItem('token')
  CustomerService.setToken(token)
CustomerService.getAll()
.then(data=>{
  setCustomers(data)
})
}
  ,
  [lisystila, reload, muokkaustila]
)

//Hakukentn Onchange tapahtumanksittelij
const handleSearchInputChange=(event)=>{
  setShowCustomers(true)
  setSearch(event.target.value.toLowerCase())
}
const editCustomer =(customer)=>{
setMuokattavaCustomer(customer)
setMuokkaustila(true)
}

return (
  <>
      <h1><nobr style={{ cursor: 'pointer' }}
              onClick={() => setShowCustomers(!showCustomers)}>Customers</nobr>
             

              {!lisystila && <button className="nappi btn btn-success" onClick={() => setLisystila(true)}>Add new</button>}</h1>

               {!lisystila && !muokkaustila &&
              <input placeholder='Search by company name' value={search} onChange={handleSearchInputChange}/>}
              {lisystila && <CustomerAdd setLisystila={setLisystila} 
                setIsPositive={setIsPositive} setMessage={setMessage} setShowMessage={setShowMessage}
                />}
 {muokkaustila && <CustomerEdit setMuokkaustila={setMuokkaustila} 
                setIsPositive={setIsPositive} setMessage={setMessage} setShowMessage={setShowMessage} muokattavaCustomer={muokattavaCustomer}
                />}

      {
          !lisystila && !muokkaustila && showCustomers && customers && customers.map(c => {
            const lowerCaseName=c.companyName.toLowerCase()
            if(lowerCaseName.indexOf(search) > -1){
              return(<Customer key={c.customerId} customer={c} reloadNow={reloadNow} reload={reload}
              setIsPositive={setIsPositive} setMessage={setMessage} setShowMessage={setShowMessage} editCustomer={editCustomer}/>)}}
            
          )
      }
  </>
)
}

export default CustomerList;
