import bootstrap from 'bootstrap'
import './App.css';
import React,{useState, useEffect} from 'react';

//propsi otetaan vastaan nimell, pilkulla eriteltyn saa monta propsia
function Posts(huomio) {

    // Usestate hook kytss, Komponentin tilan mritys
const [posts, setPosts]=useState([])
const [showPosts, setShowPosts]=useState(false)
const variant ='Light'
//useEffect hook, ajetaan aina kun komponentti latautuu
useEffect(()=>{
fetch('')
.then(response=>response.json()) //muutetaan json data javascriptiksi
.then(oliot=>setPosts(oliot))
}
  ,
  []
)

  return (
    <>
    <h2 onClick={()=>setShowPosts(!showPosts)}>Posts from typicode</h2>
    {
  showPosts&&posts&& posts.map(p=>
    
    <div className='card text-white bg-dark mb-3 kortti' key={p.id} >
    <div className='card-header'>{p.id}</div>
    <div className='card-body'>
      <h5 className='card-title'>{p.title}</h5>
      <p className='card-text'>{p.body}</p>
    </div>
  </div>
      
  )
}
    </>
    )
}

export default Posts;
