
import './App.css';
import React,{useState} from 'react';
import CustomerService from './services/Customer'



//propsi otetaan vastaan nimell customer, pilkulla eriteltyn saa monta propsia
const Customer = ({customer, setIsPositive, setMessage, setShowMessage, reload, reloadNow, editCustomer}) =>{

    // Usestate hook kytss, Komponentin tilan mritys

const [showDetails, setShowDetails]=useState(false)
const deleteCustomer=(customer)=>{

  let vastaus=window.confirm(`Remove Customer ${customer.companyName}`)
// alert("deleting customer: "+customer.companyName)
//window confirm alert ikkuna, jossa 
if (vastaus===true){
  //alert("poistolle saatiin vahvistus")
CustomerService.remove(customer.customerId).then(res => {
  if(res.status===200){
    setMessage(`Successfully removed customer ${customer.companyName}`)
    setIsPositive(true)
    setShowMessage(true)
    window.scrollBy(0,-10000)//scrollataan yls, jotta nhdn alert
    //Ilmoituksen piilotus
    setTimeout(()=> {
      setShowMessage(false)
      // window.location.reload()
    },5000)
    reloadNow(!reload)
      
}
})
.catch(error => {
  setMessage(error)
  setIsPositive(false)
  setShowMessage(true)
  window.scrollBy(0,-10000)//scrollataan yls, jotta nhdn alert

  setTimeout(() => {
    setShowMessage(false)
   }, 6000)
})

}else{
  setMessage(`Poisto peruttu onnistuneesti.`)
    setIsPositive(true)
    setShowMessage(true)
    window.scrollBy(0,-10000)//scrollataan yls, jotta nhdn alert
    //Ilmoituksen piilotus
    setTimeout(()=> {
      setShowMessage(false)},5000)
}
}

  return (
    <div className='accordioni'>
    <div className='accordion' id="accordionExample">
  <div className="accordion-item">
    <h2 className="accordion-header" id={customer.customerId}>
      <button onClick={()=>setShowDetails(!showDetails)} className="accordion-button" type="button" data-bs-toggle="collapse" data-bs-target="#collapseOne" aria-expanded="true" aria-controls="collapseOne">
       {customer.companyName}
      </button>
    </h2>
    {showDetails && 
    <div id="collapseOne" className="accordion-collapse collapse show" aria-labelledby={customer.customerId} data-bs-parent="#accordionExample">
      <div className="accordion-body tekstimusta">
        <h3>Contact person</h3><p>{customer.contactName}</p>
        <h4>Phone</h4><p>{customer.phone}</p>
        <h4>Address</h4><p>{customer.address}</p>
        <h4>City</h4><p>{customer.city}</p>
        <h4>Country</h4><p>{customer.country}</p>
        <button className="btn btn-warning"onClick={()=>deleteCustomer(customer)}>Delete</button>
        <button className="btn btn-warning"onClick={()=>editCustomer(customer)}>Edit</button>
      </div>
    </div>}
  </div>
</div></div>
    // <div className='customerDiv'>
    //   <h4 onMouseEnter={()=>setShowDetails(true)} onMouseLeave={()=>setShowDetails(false)}>{customer.companyName}</h4>

    //   {showDetails && <div className='customerDetails'>
    //     <table>
    //       <thead>
    //         <tr>
    //           <th>Contact person</th>
    //           <th>Phone</th>
    //           <th>Address</th>
    //           <th>City</th>
    //           <th>Country</th>

    //         </tr>
    //       </thead>
    //       <tbody>
    //         <tr>
    //           <td>{customer.contactName}</td>
    //           <td>{customer.phone}</td>
    //           <td>{customer.address}</td>
    //           <td>{customer.city}</td>
    //           <td>{customer.country}</td>
    //         </tr>
    //       </tbody>
    //     </table>
    //     </div>
        
    //     }
    // </div>
    )
}

export default Customer;
